<template>
  <div class="qiquanmessage">
    <el-form ref="form" :rules="rules" :model="form" label-width="80px">
      <el-row type="flex" class="row-bg">
        <!--  -->
        <el-col :span="11">
          <el-form-item label="" prop="industry">
            <el-select v-model="form.industry" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="employee.company.name">
            <el-input v-model="form.employee.company.name"></el-input>
          </el-form-item>
          <el-form-item label="" prop="employee.name">
            <el-input v-model="form.employee.name"></el-input>
          </el-form-item>
          <el-form-item label="" prop="employee.sex">
            <el-select v-model="form.employee.sex" placeholder="">
              <el-option label="" value="0"></el-option>
              <el-option label="" value="1"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="employee.jobNumber">
            <el-input v-model="form.employee.jobNumber"></el-input>
          </el-form-item>
          <el-form-item label="" prop="employee.postJob">
            <el-input v-model="form.employee.postJob"></el-input>
          </el-form-item>
          <el-form-item label="" prop="employee.joinDate">
            <!-- <el-input v-model="form.employee.joinDate"></el-input> -->
            <el-date-picker
              v-model="form.employee.joinDate"
              type="month"
              value-format="yyyyMM"
              @change="change"
              placeholder=""
            ></el-date-picker>
          </el-form-item>
          <el-form-item label="" prop="employee.seniority">
            <el-input v-model="form.employee.seniority"></el-input>
          </el-form-item>
        </el-col>
        <!--  -->
        <el-col :span="13">
          <el-form-item label-width="150px" label="" prop="employee.idCard">
            <el-input v-model="form.employee.idCard" readonly></el-input>
          </el-form-item>

          <el-form-item label-width="150px" label="" prop="employee.postArea">
            <el-select v-model="form.employee.postArea" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label-width="150px" label="" prop="approvalCode">
            <el-input v-model="form.approvalCode"></el-input>
          </el-form-item>
          <el-form-item label-width="150px" label="" prop="approvalMoney">
            <el-input v-model="form.approvalMoney"></el-input>
          </el-form-item>
          <el-form-item label-width="150px" label="" prop="equityPeriod">
            <!-- <el-input v-model="form.period"></el-input> -->
            <el-date-picker
              v-model="form.equityPeriod"
              type="monthrange"
              align="right"
              value-format="yyyyMM"
              unlink-panels
              range-separator=""
              start-placeholder=""
              end-placeholder=""
            ></el-date-picker>
          </el-form-item>
          <!-- , -->
          <!-- <el-form-item label-width="150px" label="" prop="shareStartDate">
            <el-date-picker
              v-model="form.shareStartDate"
              type="month"
              value-format="yyyyMM"
              @change="change"
              placeholder=""
            ></el-date-picker>
          </el-form-item>
          <el-form-item label-width="150px" label="" prop="shareEndDate">
            <el-date-picker
              v-model="form.shareEndDate"
              type="month"
              value-format="yyyyMM"
              @change="change"
              placeholder=""
            ></el-date-picker>
          </el-form-item>-->

          <el-form-item label-width="150px" label="" prop="shareMoney">
            <el-input v-model="form.shareMoney"></el-input>
          </el-form-item>
          <el-form-item label="" prop="cashDate">
            <el-date-picker
              v-model="form.cashDate"
              type="year"
              value-format="yyyy"
              @change="change"
              placeholder=""
            ></el-date-picker>
          </el-form-item>
        </el-col>
      </el-row>
      <el-form-item label="">
        <el-input type="textarea" v-model="form.desc"></el-input>
      </el-form-item>
      <el-form-item>
        <!-- <el-button type="primary" @click="onSubmit"></el-button> -->
        <el-button type="primary" @click="amend"></el-button>
        <el-button type="primary" @click="exit"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
// import axios from "axios";
export default {
  data() {
    return {
      employee: this.$store.state.employee,
      form: {
        approvalCode: "", // 
        industry: "", // 
        approvalMoney: "", // 
        cashDate: "", // 
        // shareStartDate: "", // 
        // shareEndDate: "", //  
        shareMoney: "", // 
        equityPeriod: "", // 
        employee: {
          name: "", // 
          company: {
            name: ""
          }, // id
          idCard: this.$store.state.employee.employee.idCard, // 
          postArea: "", // 
          jobNumber: "", // 
          joinDate: "2018/03/08", // 
          postJob: "", // 
          seniority: "", // 
          sex: "", // 
          sts: "" // 
        }
      },
      rules: {
        industry: [
          { required: true, message: "", trigger: "change" }
        ],
        "employee.company.name": [
          { required: true, message: "", trigger: "blur" }
        ],
        "employee.name": [
          { required: true, message: "", trigger: "blur" }
        ],
        "employee.sex": [
          { required: true, message: "", trigger: "change" }
        ],
        "employee.jobNumber": [
          { required: true, message: "", trigger: "blur" }
        ],
        "employee.postJob": [
          { required: true, message: "", trigger: "blur" }
        ],
        "employee.joinDate": [
          {
            type: "string",
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        equityPeriod: [
          { required: true, message: "", trigger: "change" }
        ],
        "employee.seniority": [
          { required: true, message: "", trigger: "blur" }
        ],
        cashDate: [
          { required: true, message: "", trigger: "blur" }
        ],
        "employee.idCard": [
          { required: true, message: "", trigger: "blur" }
        ],
        "employee.postArea": [
          { required: true, message: "", trigger: "blur" }
        ],
        approvalCode: [
          { required: true, message: "", trigger: "blur" }
        ],
        approvalMoney: [
          { required: true, message: "", trigger: "blur" }
        ],
        // shareStartDate: [{ required: true, message: "", trigger: "change" }],
        // shareEndDate: [{ required: true, message: "", trigger: "change" }],
        shareMoney: [
          { required: true, message: "", trigger: "blur" }
        ]
      }
    };
  },
  created() {},
  methods: {
    amend() {
      this.$refs.form.validate(valid => {
        if (valid) {
          console.log(this.form.equityPeriod);
          this.form.equityPeriod = this.form.equityPeriod.join("-");
          this.$axios
            .put(
              "/basicEquity/edit/" + this.employee.id,
              JSON.stringify(this.form),
              {
                headers: {
                  "Content-Type": "application/json;charset=UTF-8"
                }
              }
            )
            .then(res => {
              console.log(res, "amend");
              if (res.data.code == "400") {
                this.$message.info(res.data.msg);
              } else {
                this.$message.info(res.data.msg);
              }
            })
            .catch(erro => {
              console.log(erro);
            });
        } else {
          this.$message.info(",");
          return false;
        }
      });
    },
    change() {
      // 
      console.log("change");
    },
    exit() {
      this.$store.state.isshow = false;
    }
  }
};
</script>

<style lang="scss" scoped>
</style>


